body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  background: #bdebfc; }

.App {
  text-align: center;
  background-image: url("/images/portfolio/dog-2785074_1920.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh; }
  .App .main {
    color: white;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    font-family: 'Titillium Web', sans-serif; }
  .App .title {
    padding: 45px 15px 0;
    font-size: 3em; }
  .App .twitter-timeline-wrapper {
    margin: 25px 0 50px 0;
    min-height: 500px; }
  .App .insta-image {
    margin: 10px;
    margin-top: 60vh; }
    .App .insta-image iframe {
      margin: 0 auto !important; }
  .App footer {
    background: black;
    width: 100%;
    height: 40px;
    padding: 15px 0; }
    .App footer a {
      color: #d939dc; }

